@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
#content-logo {
  width: 100%;
  padding: 40px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

#content-logo svg{
  width: 73.1px;
  height: 73.1px;
}

#content-logo svg rect{
  fill:#7b1fa2;
}

#content-logo svg circle{
  fill:#fff;
}

#content-logo svg g{
  isolation:isolate;
}

#content-logo svg path{
  fill:#7b1fa2;
}

#content-logo .top-eye{
  transition: height .3s ease;
}

#content-logo .bot-eye{
  transition: height .3s ease, transform .3s ease;
}

#content-logo svg:hover .top-eye {
  height: 18.5px;
}

#content-logo svg:hover .bot-eye {
  transform: translateY(-18px);
  height: 18.5px;
}
#messages-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  overflow: hidden;
}

#messages-container .message {
  max-width: 80%;
  word-break: normal;
  background: #7B1FA2;
  color: #fff;
  border-radius: 16px;
  padding: 16px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin-top: 16px;
  transition: opacity .6s, transform .6s;
  font-size: 16px;
  line-height: 1.5;
}

#messages-container .message.bot {
  background: #fff;
  color: #000;
  align-self: flex-start;
  border-bottom-left-radius: 0;
}

#messages-container .message.user {
  align-self: flex-end;
  border-bottom-right-radius: 0;
  text-align: right;
}

#messages-container .message.hidden {
  opacity: 0;
  transform: translateY(-20px);
}
#messages-input {
  padding: 40px 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}

#messages-input input{
  width: calc(100% - 48px);
  height: 48px;
  background: #7B1FA2;
  border-radius: 32px;
  border: none;
  padding: 0 24px;
  font-size: 16px;
  color: #fff;
}

#messages-input input::-webkit-input-placeholder {
  color: #fff;
  opacity: .5;
}

#messages-input input:-ms-input-placeholder {
  color: #fff;
  opacity: .5;
}

#messages-input input::-ms-input-placeholder {
  color: #fff;
  opacity: .5;
}

#messages-input input::placeholder {
  color: #fff;
  opacity: .5;
}

#messages-input button{
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 8px;
  background: #B0196C;
  transition: background .5s ease;
  border: none;
  border-radius: 24px;
  cursor: pointer;
}
#messages-input button:hover{
  background: #7c0041;
}

#messages-input button img{
  width: 24px;
  height: 24px;
  transform: translateX(-1px);
}


@media (max-width: 1000px) {
  #messages-input {
    padding-bottom: 24px;
  }
}
@font-face {
  font-family: 'Gotham';
  src: local('Gotham'), url(/static/media/GothamBook.b54724f5.ttf) format('truetype');
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html, body, #root {
  height: 100%;
}

body {
  font: 400 16px 'Gotham', sans-serif;
  -webkit-font-smoothing: antialiased;
  background: #000;
  color: #fff;
}

input, button {
  font: 400 16px 'Gotham', sans-serif;
  color: #fff;
}

#site-container {
  display: flex;
  flex-direction: column;
  width: 480px;
  max-width: 90%;
  height: 100%;
  margin: 0 auto;
}

